.edit-container .edit-wrapper .w-e-toolbar .w-e-menu {
  z-index: 999 !important;
}

.edit-container .edit-wrapper .w-e-text-container {
  min-height: 4rem !important;
  padding: 0.133333rem 0.2rem;
  border: none !important;
}

.edit-container .operate-btn {
  margin: 0.266667rem 0 0 0.266667rem;
  float: right;
}

.edit-container .ant-upload img {
  max-width: 100%;
}

@media screen and (max-width: 960px) {
  .container {
    min-width: 100%;
  }

  .article-container {
    padding: 0.426667rem;
  }

  .article-container > div {
    overflow: none;
  }

  .article-container > div .right {
    display: none;
  }

  .article-container > div .left {
    float: none;
    width: 100%;
  }

  .article-detail-container {
    padding: 0.426667rem;
  }

  .article-detail-container .left,
  .article-detail-container .right {
